import React, { createElement } from 'react'
import { useMeasure } from 'react-use';

import Box from './Box'
import Text from './Text'
import { responsive } from './ThemeProvider/theme';

const LeftTitle = ({ title, description, notePt, smallTilte, descPt, titleSpacing, big, note, multiple}) => {
  const [ref, { width }] = useMeasure();
  return (
    <>
      {big ? <Text fontSize="1.125rem" letterSpacing={responsive('1px', '3px')} fontWeight={responsive('700', '500')}>{smallTilte}</Text> : <Text.H4>{smallTilte}</Text.H4>}
      <Box.Inline ref={ref}>
        {createElement(big ? Text.H1 : Text.H2,
          {
            letterSpacing: titleSpacing || '2px'
          },
          title
        )}
      </Box.Inline>
      <Box
        textAlign="left"
        fontWeight="400"
        lineHeight={responsive(1.5, 1.8)}
        fontSize={responsive('0.875em', '1em')}
        pt={descPt || responsive('0.625rem', '1.25rem')}
        width={responsive('auto', width * (multiple || 1))}
        whiteSpace="pre-wrap"
      >
        {description}
      </Box>
      {note && (
        <Box width={responsive('auto', width * (multiple || 1))} pt={notePt || '0.625rem'}>
          {note.map((n, i) => (
            <Text.Note key={i} textAlign="left">
              {n}
            </Text.Note>
          ))}
        </Box>
      )}
    </>
  )
}

export default LeftTitle
