import React from 'react'
import Flex from './Flex'
import Box from './Box'
import { responsive } from './ThemeProvider/theme'

const PageModule = ({ right, left, leftProps, rightMarginTop, ...props }) => {
  return (
    <Flex
      flexDir={responsive('column', 'row')}
      justify="center"
      align="center"
      {...props}
    >
      <Box textAlign={responsive('center', 'left')} {...leftProps}>
        {left}
      </Box>
      <Box mt={responsive(rightMarginTop || '2em', 0)} flex={1} width={responsive('100%', '50%')}>
        {right}
      </Box>
    </Flex>
  )
}

export default PageModule
